import React from "react";
import Grid from "@material-ui/core/Grid";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";

const styles = {
  tall: { height: "100%" }
};

const TwoCol = ({ left, right, classes }) => {
  return (
    <Grid
      container
      direction="row"
      justify="center"
      alignItems="center"
      classes={{ container: classes.tall }}
      spacing={16}
    >
      <Grid item md={6} xs={12} classes={{ item: classes.tall }}>
        {left}
      </Grid>
      <Grid item md={6} xs={12} classes={{ item: classes.tall }}>
        {right}
      </Grid>
    </Grid>
  );
};

TwoCol.propTypes = {
  left: PropTypes.node.isRequired,
  right: PropTypes.node.isRequired
};

export default withStyles(styles)(TwoCol);
